.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  padding: 20px;
  color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  width: 100%;
  z-index: 9999;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: 16px;
  padding-top: 100px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}
.article-image {
  width: 150px;
}

.media__thumb {
  position: relative;
}
.media__checkbox {
  position: absolute;
  bottom: 10px;
  left: 10px;
}

.media__title {
  font-weight: bold;
}

.media__footer {
  font-size: 12px;
}

.media__author {
  font-weight: bold;
}


